.ad__card {
    padding: 19px;
    background-color: $accent;
    border: 1px solid $border;
    .post__main {
        gap: 0;
    }
    .post__title {
        cursor: pointer;
        margin-bottom: 10px;
        &:hover {
            color: $darkBlue;
        }
    }
    .post__actions {
        .icon {
            width: 18px;
            height: 18px;
            fill: $grayText;
        }
    }
    .post__text {
        line-height: 170%;
        margin-bottom: 15px;
    }
    .post__img {
        width: 100%;
        height: 470px;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

@media (max-width: 1200px) {
    .ad__card {
        .post__img {
            height: 340px;
        }
    }
}
