.title__link {
    display: inline-flex;
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    color: $black;
}

.underline__link {
    display: inline-flex;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    text-decoration-line: underline;
    color: $darkBlue;
}

.underline__link:hover {
    color: $deepBlue;
}

.comment__link {
    font-weight: 500;
    font-size: 13px;
    line-height: 100%;
    color: $grayText;
    display: block;
}

.comment__link:hover {
    color: $darkBlue;
}

.vacancy__link {
    font-weight: 400;
    font-size: 14px;
    line-height: 170%;
    color: $darkBlue;
    cursor: pointer;
}

.vacancy__link:hover {
    color: $deepBlue;
}

.read-smore__link {
    font-weight: 400;
    font-size: 14px;
    line-height: 170%;
    color: $darkBlue;
    cursor: pointer;
    transition: all 0.2s ease;
    &:hover {
        color: $deepBlue;
    }
}

.form__link {
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
}

.form__link:hover {
    color: $darkBlue;
}

.reg__link {
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 100%;
    color: $darkBlue;
}

.reg__link:hover {
    color: $deepBlue;
}

.profile__link {
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
}

.profile__link:hover {
    color: $darkBlue;
}

.password__change {
    display: inline-flex;
    font-weight: 400;
    font-size: 13px;
    line-height: 15px;
    color: $darkBlue;
}

.password__change:hover {
    color: $deepBlue;
}
